import React from "react";
import { Button } from "antd";
import "antd/dist/antd.css";
import "./Intro.css";
import { NavLink } from "react-router-dom";

const Intro = () => {
  return (
    <div className="bg">
      <div>
        <h1 className="intro--title">Constructive Design</h1>
        <p>
          Aenean fermentum vestibulum ipsum, ut pretium erat sodales sodales.
          Pellentesque quis orci vitae dui commodo sodales et ut quam. Etiam
          vitae egestas purus, ut malesuada enim.
        </p>
        <NavLink to="/Product">
          <Button className="btn" size="large">
            Continue...
          </Button>
        </NavLink>
      </div>
    </div>
  );
};

export default Intro;
