import { NavLink, Outlet } from "react-router-dom";
import React, { useState } from "react";
import { Layout, Drawer } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import "./Template.css";
import {
  HomeOutlined,
  HighlightOutlined,
  MessageOutlined,
  TeamOutlined,
} from "@ant-design/icons";

const { Sider, Content } = Layout;

const Template = () => {
  const [open, setOpen] = useState(false);

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };
  return (
    <>
      <div className="comp-screen">
        <Sider className="menu">
          <div>
            <NavLink to="/react-antd-template/">
              <h1>
                <HomeOutlined className="ic" />
                Introduction
              </h1>
            </NavLink>
          </div>
          <div>
            <NavLink to="/react-antd-template/Product">
              <h1>
                <HighlightOutlined className="ic" />
                Our Product
              </h1>
            </NavLink>
          </div>
          <div>
            <NavLink to="/react-antd-template/Company">
              <h1>
                <TeamOutlined className="ic" />
                Our Company
              </h1>
            </NavLink>
          </div>
          <div>
            <NavLink to="/react-antd-template/Contact">
              <h1>
                <MessageOutlined className="ic" />
                Contact Us
              </h1>
            </NavLink>
          </div>
        </Sider>
      </div>
      <div className="mobile-screen">
        <MenuOutlined className="burger-icon" onClick={showDrawer} />
        <Drawer
          placement="left"
          onClose={onClose}
          open={open}
          className="drawer"
        >
          <Sider className="menu">
            <div>
              <NavLink to="/react-antd-template/">
                <h1>
                  <HomeOutlined className="ic" />
                  Introduction
                </h1>
              </NavLink>
            </div>
            <div>
              <NavLink to="/react-antd-template/Product">
                <h1>
                  <HighlightOutlined className="ic" />
                  Our Product
                </h1>
              </NavLink>
            </div>
            <div>
              <NavLink to="/react-antd-template/Company">
                <h1>
                  <TeamOutlined className="ic" />
                  Our Company
                </h1>
              </NavLink>
            </div>
            <div>
              <NavLink to="/react-antd-template/Contact">
                <h1>
                  <MessageOutlined className="ic" />
                  Contact Us
                </h1>
              </NavLink>
            </div>
          </Sider>
        </Drawer>
      </div>
      <Layout className="content">
        <Content>
          <Outlet />
        </Content>
      </Layout>
    </>
  );
};

export default Template;
