import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import CompanyPage from "./Pages/CompanyPage";
import ContactPage from "./Pages/ContactPage";
import IntroPage from "./Pages/IntroPage";
import ProductPage from "./Pages/ProductPage";
import Template from "./Pages/Template";
function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route path="/react-antd-template/" element={<Template />}>
            <Route index element={<IntroPage />}></Route>
            <Route
              path="/react-antd-template/Product"
              element={<ProductPage />}
            ></Route>
            <Route
              path="/react-antd-template/Company"
              element={<CompanyPage />}
            ></Route>
            <Route
              path="/react-antd-template/Contact"
              element={<ContactPage />}
            ></Route>
          </Route>
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
