import React from "react";
import { Carousel } from "antd";
import "./Product.css";

const Product = () => {
  return (
    <>
      <div className="product-bg">
        <div>
          <Carousel effect="fade" className="carsl">
            <div>
              <img src="./image/gallery-img-01-tn.jpg" alt="" />
            </div>
            <div>
              <img src="./image/gallery-img-02-tn.jpg" alt="" />
            </div>
            <div>
              <img src="./image/gallery-img-03-tn.jpg" alt="" />
            </div>
            <div>
              <img src="./image/gallery-img-04-tn.jpg" alt="" />
            </div>
            <div>
              <img src="./image/gallery-img-05-tn.jpg" alt="" />
            </div>
            <div>
              <img src="./image/gallery-img-06-tn.jpg" alt="" />
            </div>
          </Carousel>
        </div>
      </div>
    </>
  );
};

export default Product;
